<template>
<div class="main-user-content">
    <Display
    :title=articleInformation.title
    headImg="https://s1.328888.xyz/2022/08/17/3bd7B.webp"
    :class_=articleInformation.reviewerName
    :writer=articleInformation.contributorName
    :time=articleInformation.releaseTime
    :content=articleInformation.text
    />
    </div>
</template>
<script>
import Display from "@/components/Display.vue";
import global from "@/global.vue";
export default{
    components:{
        Display,
        global
    },
    created(){
        fetch(global.rootUrl+"art/select/"+this.$route.fullPath.split("=")[this.$route.fullPath.split("=").length-1])
        .then(res=>res.json())
        .then(res=>{
            this.articleInformation = res.result;
            this.articleInformation.text = this.articleInformation.text.split("</p>")
        })
    },
    data(){
        return{
            articleInformation:{}
        }
    }
}
</script>